.gallery{
    -webkit-column-count: 3;
    column-count: 3;
    -webkit-column-width: 33%;
    column-width: 33%;
    padding: 0 12px;
}

.gallery .photos{
    transition: all 350ms ease;
    cursor: pointer;
    margin-bottom: 12px;
    
}

.gallery .photos:hover{
    -webkit-filter: opacity(.8);
            filter: opacity(.8);
}

.footer {
    display: flex;
    height: 280px;
}

@media (max-width: 991px){
    .gallery{
        -webkit-column-count: 2;
        column-count: 2;
    }
}

@media (max-width: 480px){
    .gallery{
        -webkit-column-count: 1;
        column-count: 1;
        -webkit-column-width: 100%;
        column-width: 100%;
        
    }

}


.model{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center; 
    background-color: black;
    transition: opacity .4s ease, visibility .4s ease, -webkit-transform .5s ease-in-out;
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out;
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
    visibility: hidden;
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
    overflow: hidden;
    z-index: 999;
}

.model.open{
    visibility: visible;
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    display: flex;
    justify-items: center;
    align-items: center;
}

.model img{
    width: auto;
    max-width: 50%;
    height: auto;
    max-height: 100%;
    display: block;
    line-height: 0;
    box-sizing: border-box;
    padding: 20px 0 20px;
    margin: 0 auto;
}

.model.open svg{
    position: fixed;
    top: 10px;
    right: 10px;
    width: 2rem;
    height: 2rem;
    padding: 5px;
    background-color: (rgba0,0,0,0.4);
    color: #ffffff;
    cursor: pointer;
} 

.contentModel p {
